import { h, Host } from "@stencil/core";
export class RaulCarouselCard {
    render() {
        return (h(Host, { class: "r-carousel__slide__container" },
            h("div", { class: "r-carousel__slide__container__card" },
                h("slot", null))));
    }
    static get is() { return "raul-carousel-card"; }
}
